.log-in-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    
    &__sign-up {
        justify-self: center;;
    }
    &__brand-logo {
        position: absolute;
        top: 25px;
    }
    .ellipse {
        position: absolute;
        
    }
    &__ellipse_bg-1 {
        top: calc(-25px - 3.125vw);
        right: -65px;
        width: calc(50px + 30vw); height: calc(50px + 30vw);
    }
    &__ellipse_bg-2 {
        top: calc(34px + 30vw);
        right: -7px;
        width: 20px; height: 20px;
    }
}